import { t } from 'sn-translate';
import { parseArabic } from '@devsnc/sn-controls-common';

const intFormatRegex = /^([+-]?[0-9]+)?$/g;
const floatFormatRegex = /^([+-]?([0-9]*[.])?[0-9]+)?$/g;

export const moveDecimalOverTwoPlaces = value => {
	// do manipulations because javascript can't math
	// and has precision errors when trying to do
	// value = value * 0.01 ;
	// which is needed because
	// Intl.NumberFormat maps 1 to 100%
	// Ergo, user input 23.4 ends up becoming 23.399999999999999%

	const decimalValue = value.toString().split('.')[1];
	const decimalLength = (decimalValue && decimalValue.length) || 0;

	return parseFloat((value / 100).toFixed(decimalLength + 2));
};

export const parseSeparator = replacement => regex => value =>
	typeof value === 'string' ? value.replace(regex, replacement) : value;

export const format = (value, decimal, isPercent, locale) => {
	let error;
	// if the value is not set skip formatting
	if (value === null || value === undefined || value === '') {
		value = '';
	} else {
		// else check if value is a float or int and format it if so
		const floatFormat = new RegExp(floatFormatRegex);
		const intFormat = new RegExp(intFormatRegex);

		// We check if value is a float	and format it if so
		const floatMatch = floatFormat.test(value);
		const intMatch = intFormat.test(value);
		if ((decimal && floatMatch) || (!decimal && intMatch)) {
			// when useAllFractionsDigits is pased in we want to force Intl.NumberFormat to persist all
			// fraction digits, otherwise don't set these properties and rely on the locale+currency to
			// drive.
			const formatOptions = {
				minimumFractionDigits: 0,
				maximumFractionDigits: decimal ? 20 : 0
			};
			if (isPercent) {
				formatOptions.style = 'percent';
				value = moveDecimalOverTwoPlaces(value);
			}
			value = new Intl.NumberFormat(locale, formatOptions).format(value);
		} else {
			error = {
				type: 'error',
				message: decimal
					? t('{0} is not a valid number', value)
					: t('{0} is not a valid integer', value)
			};
		}
	}
	return {
		value,
		error
	};
};

export const parse = (value, decimal, isPercent, locale) => {
	let error;
	// if the value is not set skip parsing
	if (value === null || value === undefined || value === '') {
		value = '';
	} else {
		// else check if value is a float or int and attempt to parse
		const floatFormat = new RegExp(floatFormatRegex);
		const intFormat = new RegExp(intFormatRegex);
		const numWithThousands = new Intl.NumberFormat(locale).format('1111');
		const numWithFractions = new Intl.NumberFormat(locale).format('1.11');

		const thousandSeparator =
			numWithThousands.length === 4 ? '' : numWithThousands.slice(1, 2);
		const decimalSeparator = numWithFractions.slice(1, 2);
		const strip = parseSeparator('');
		let sanitizers = [
			parseArabic,
			strip(new RegExp(`\\${thousandSeparator}`, 'g')),
			parseSeparator('.')(new RegExp(`\\${decimalSeparator}`, 'g')),
			strip(/[\s\u00A0]/g)
		];

		if (isPercent) {
			sanitizers = [...sanitizers, strip('%')];
		}

		const processedValue = sanitizers.reduce(
			(sanatizedValue, parser) => parser(sanatizedValue),
			value
		);
		const floatMatch = floatFormat.test(processedValue);
		const intMatch = intFormat.test(processedValue);
		if ((decimal && floatMatch) || (!decimal && intMatch)) {
			value = processedValue;
		} else {
			error = {
				type: 'error',
				message: decimal
					? t('Could not parse {0} as a number', value)
					: t('Could not parse {0} as an integer', value)
			};
		}
	}
	return {
		value,
		error
	};
};
