import React from 'react';
import react from '@servicenow/ui-renderer-react';
import PropTypes from 'prop-types';
import { noop, omit } from 'lodash';

import {
	fieldPropTypes,
	transformState,
	ForwardProperties
} from '@devsnc/sn-controls-common';
import * as NumberFormatter from './numberFormat';

import { SNInput } from '@devsnc/sn-record-input';
import { ConnectedInput } from '@devsnc/sn-record-input-connected';
import { withState } from '@devsnc/sn-record-input-connected';
import { createCustomElement } from '@servicenow/ui-core';

/**
 * Allows users to enter and edit number values
 */
const NowNumber = withState(
	class extends React.PureComponent {
		static componentName = 'now-record-number';
		render() {
			const { name, invalid, messages } = this.props;
			const attrs = omit(
				this.props,
				'decimal',
				'integer',
				'locale',
				'percent',
				'format'
			);

			return (
				<ConnectedInput
					{...attrs}
					maxlength="14"
					invalid={invalid}
					messages={messages}
					name={name + '_input'}
					type="text"
					align="end"
				/>
			);
		}
	},
	{
		parse: (value, props) => {
			const decimal = !props.integer && props.decimal;
			return NumberFormatter.parse(value, decimal, props.percent, props.locale);
		},
		format: (value, props) => {
			if (props.format === 'none') return { value, error: undefined };
			const decimal = !props.integer && props.decimal;
			return NumberFormatter.format(
				value,
				decimal,
				props.percent,
				props.locale
			);
		},
		stopBubbling: ['SN_RECORD_INPUT_CONNECTED']
	}
);

NowNumber.style = SNInput.style;
NowNumber.propTypes = {
	...fieldPropTypes,

	/**
	 * Initial value for the input.
	 */
	initialValue: PropTypes.string,

	/**
	 * flag to indicate whether to support fractions or not
	 */
	decimal: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),

	/**
	 * System locale setting used to format the number
	 */
	locale: PropTypes.string
};

NowNumber.defaultProps = {
	value: '',
	integer: false,
	decimal: true,
	percent: false,
	locale: window.navigator.language,
	dispatch: noop
};

/**
 *
 *  An input number component that can either represent an integer, a decimal/float or a
 *  percent_complete field type.
 *
 * ```jsx
 * <now-record-number name="number" decimal="true" intialValue="12345" />
 * ```
 *
 * @seismicElement now-record-number
 * @summary An input number component that can either represent an integer, a decimal/float or a
 * percent_complete field type.
 *
 * */
createCustomElement('now-record-number', {
	type: react,
	view: ForwardProperties(NowNumber),
	styles: NowNumber.style,
	properties: {
		/**
		 * aria-describedby
		 *
		 * Accessible Attribute to indicate the IDs of the elements that describe the object.
		 * Which element (by ID) provides more description for the component
		 * @type {string}
		 * @private
		 */
		ariaDescribedby: {},
		/**
		 * aria-label
		 *
		 * Accessible Attribute to define a string that labels the current element.
		 * Accessible label for the component. Only needed when the `label` prop is not
		 * descriptive enough on its own.
		 * @type {string}
		 * @private
		 */
		ariaLabel: {},
		/**
		 * aria-labelledby
		 *
		 * Accessible Attribute establishes relationships between objects and their label(s)
		 * Which element (by ID) acts as a label for the component
		 * @type {string}
		 * @private
		 */
		ariaLabelledby: {},
		/**
		 * Causes the component to receive focus as soon as it has finished
		 * mounting.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {string | boolean}
		 */
		autofocus: {},
		/**
		 * An object whose items are all aria properties in the format 'aria-*' : 'value'
		 * @type {{'aria-*': string }}
		 */
		configAria: {},
		/**
		 * A string that identifies if the number field is of the decimal type,
		 * can only hold the string values "true" (case-sensitive) and "false",
		 * indicating whether to support fractions or not. If `integer` is set to
		 * true this prop gets over written.
		 * @type {string | boolean}
		 * @private
		 */
		decimal: { default: true },
		/**
		 * Additional content describing the field, usually to provide further
		 * instruction to the user.
		 * @type {string}
		 */
		description: {},
		/**
		 * Format option for number fields. If none, the number will not be formatted.
		 *
		 * @type {string}
		 */
		format: {},
		/**
		 * The first value that the component has when rendered
		 * @type {string}
		 */
		initialValue: {},
		/**
		 * Set to true if the field should be an integer and not a decimal type.
		 * @type {boolean}
		 */
		integer: { default: false },
		/**
		 * Specifies whether the current value is not valid.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {string | boolean}
		 */
		invalid: { default: false },
		/**
		 * Label for the field that is shown to the user.
		 * @type {string}
		 */
		label: {},
		/**
		 * System locale setting used to format the number.
		 * @type {string}
		 */
		locale: { default: window.navigator.language },
		/**
		 * Array of message objects that must contain type and message itself.
		 * @type {Array<{type: ("error"|"warning"|"success"|"liveUpdate"|"info"|"suggestion"), type:Array<string,Object<{string, getMessage:function}>> }>}
		 */
		messages: { default: [] },
		/**
		 * Name for the component. Any events dispatched by the component will use this name.
		 * @type {string}
		 */
		name: {},
		/**
		 * A string  that identifies if the number field is percent_complete,
		 * can only hold the string values "true" (case-sensitive) and "false".
		 * @type {string | boolean}
		 */
		percent: { default: false },
		/**
		 * Callback used when an intermediate value has changed.
		 * @type {function}
		 * @private
		 */
		onStagedValueChange: {},
		/**
		 * Callback used when the value has changed. Very similar to an `onChange`
		 * event, but this may also fire when the component formats its value
		 * onBlur.
		 *
		 * @param {object} event - The event payload
		 * @param {string} event.name - The name of the component that triggered the event
		 * @param {any}    event.value - The new value for the component
		 * @param {string} event.displayValue - The human-readable represenation of the value
		 *
		 * @param {object} event.error - If the current value is invalid, this propery will be set. When the current value is valid, this property will be null.
		 * @param {string} event.error.type - Error type
		 * @param {string} event.error.message - Description of why the value is invalid
		 * @type {function}
		 * @private
		 */
		onValueChange: {},
		/**
		 * Specifies that the user cannot modify the value of the component.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {boolean}
		 */
		readonly: { default: false },
		/**
		 * Whether or not the user must fill in a value for this component.
		 * Can be specified as a boolean or the string "true"
		 * @type {boolean}
		 */
		required: { default: false },
		/**
		 * Specifies that the field should be rendered right-to-left.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {boolean}
		 * @private
		 */
		reversed: { default: false },
		/**
		 * Underlying data for the component.
		 * @type {string}
		 * @private
		 */
		value: { default: '' }
	},
	dispatches: {
		/**
		 * Dispatched on each value change on the input.
		 * @type {{ value: string }}
		 */
		'NOW_RECORD_NUMBER#STAGED_VALUE_CHANGED': {},
		/**
		 * Dispatched on blur.
		 * @type {{ value: string, error: { type: string, message: string } }}
		 */
		'NOW_RECORD_NUMBER#VALUE_CHANGED': {}
	},
	transformState
});
